import React from 'react';
import Card from 'react-bootstrap/Card';
import Col from 'react-bootstrap/Col';
import usePersons from '../../api/newPerson';
import { useNavigate } from "react-router-dom";

export default function DisplayCollection({deletePlaylist,playlist,person,books}){
const navigate = useNavigate();
const img = playlist.imgLink;
const title = playlist.title;
const description = playlist.description;
const firstName= person.firstName
const lastName =  person.lastName


const showBooks = () => {
      const goTo = () =>{
     
        navigate(`/bookcollection/${playlist.id}`);
      }
      goTo();

}
const deleteP = () => {
         deletePlaylist(playlist.id);
}

return(
<div >
        <div  style = {{backgroundColor:"#6b5b95",maxWidth:"16rem"}}className = "card">
            <Card style={{ width:"18rem" }}>
            <Card.Body>
            <img src = {img} className = "img-thumbnail" alt="BoekThumbnail"></img>

            <Card.Title>
            <blockquote className="blockquote">
                    <p>
                    {title}
                    </p>
            </blockquote>
            <figcaption className="blockquote-footer">
                    <p>{description}</p>
                    </figcaption>
            </Card.Title>

            <Card.Subtitle className="mb-2 text-muted">{`Creator: ${firstName} ${lastName}`}</Card.Subtitle>
          
            <button style ={{backgroundColor:"#6b5b95",color:"white"} } onClick = {showBooks} >See All Books</button>
            <button style ={{backgroundColor:"red",color:"white"} } onClick = {deleteP} >Delete</button>
            {/* <button style ={{backgroundColor:"#6b5b95",color:"white"} } value = "A" onClick = {handleClick} >Read More</button> */}
           

                 </Card.Body>
                 </Card> 
    </div>

    </div>
)
            
        
    
      
}