import { useAuth0 } from '@auth0/auth0-react';
import LoginButton from './LoginButton';
import LogoutButton from './LogoutButton';

export default function AuthenticationButton() {
  const {
    isAuthenticated,
    user,
  } = useAuth0(); // 👈 1

  if (isAuthenticated) { // 👈 2
    const { name, picture, givenName } = user;
    return (
      <div className="d-flex flex-row align-items-center">
        <div className="col">
          <img src={picture} alt={givenName} className="rounded" style={{width:"60px"}}/>
        </div>
        <div className="col">
         <p style ={{color:"white",marginRight:"20px"}}> {name}</p>
        </div>
        <div className="col" style ={{width:"120px"}}>
          <LogoutButton />
        </div>
      </div>
    );
  }

  return <LoginButton />;
}
