import { useAuth0 } from '@auth0/auth0-react';
import { useCallback } from 'react';

function LoginButton() {
  const { loginWithRedirect } = useAuth0();

  const handleLogin = useCallback(

    async () => {
      loginWithRedirect();
    },
    [loginWithRedirect],
  );

  return (
    <div>
          <i className={["fa-solid fa-user "]} style ={{color:"white", position:"fixed", right:"85px", top:"20px"}} onClick= {()=>  console.log("Clicked")}></i>

    <button
      type="button"
      style = { {width:"120px"}}
      className="btn btn-primary"
      onClick={handleLogin}
    >
      Log In
    </button>
    </div>
  );
}

export default LoginButton;
